                duration: "Past 1 Year",
                values: [{name: "Kiambu", id: "022" }],
                advanced: false,
            })
            this.$router.push(`/pathogens/${this.$route.params.pathogenId}/antibiogram`) 
        },
        len (arr) { return arr.length }
